.hamMushroominstructionTitle {
  color: #289130;
  font-size: 20px;
  margin-bottom: 5px;
}

div.hamMushroomline {
  text-align: center;
  background-image: url(./images/line.png);
  width: 100%;
  height: 12px;
  background-repeat: no-repeat;
  background-size: contain;
}
div.hamMushroomSteps div.step1,
div.hamMushroomSteps div.step2,
div.hamMushroomSteps div.step3 {
  display: flex;
  justify-content: flex-start;
  color: #289130;
}

@media (min-width: 600px) {
  .hamMushroominstructionTitle {
    font-size: 35px;
  }
  div.hamMushroomline {
    height: 20px;
  }
}
