div.hamMushroomInformationsBackground {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(./images/skos3.png);
}

img.hamMushroomArrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
  content: url("./images/arro-mobile.png");
}

.hamMushroomNutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #fffad4;
  justify-content: space-around;
}

p.hamMushroomTitle {
  font-size: 20px;
  margin-bottom: 5px;
  color: #fffad4;
}

div.hamMushroomLineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
  margin-bottom: 15px;
}
div.hamMushroomLineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
}

div.hamMushroomNutritionalValue {
  display: flex;
}

div.hamMushroomNutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.hamMushroomAlergensDescription {
  color: #fffad4;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.hamMushroomLineInformations {
    width: 310px;
  }
  div.hamMushroomLineAlergens {
    width: 140px;
  }
  p.hamMushroomTitle {
    font-size: 35px;
  }
  div.hamMushroomNutritionalValue,
  .hamMushroomAlergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.hamMushroomArrow {
    margin: 20px auto;
    content: url("./images/arrow.png");
  }
  div.hamMushroomInformationsBackground {
    display: block;
    height: 0;
    background-image: none;
  }

  p.hamMushroomTitle {
    font-size: 23px;
    color: #289130;
    font-weight: 700;
  }
  div.hamMushroomLineInformations {
    width: 240px;
    height: 12px;
    background-image: url(./images/lineRed.png);
    background-size: contain;
    background-position: center;
  }

  div.hamMushroomLineAlergens {
    width: 120px;
    height: 11px;
    background-image: url(./images/lineRed.png);
    background-position: center;
  }
  .hamMushroomNutritionalValues {
    color: #289130;
    transition: 0.1s;
  }

  div.hamMushroomNutritionalValue,
  .hamMushroomAlergensDescription {
    font-size: 12px;
  }

  .hamMushroomAlergensDescription {
    color: #289130;
    display: block;
  }
}
